import React, {Component} from 'react';
import axios from "axios/index";

class saveFile extends Component{
    constructor(){
        super();
        this.state = {
            selectedFile: null,
            success: null,
            error: null
        }
    }

    handleFileChange = event =>{
        this.setState({
            selectedFile: event.target.files[0],
            success: null,
            error: null
        });
    };

    sendCSV = (e) => {
        e.preventDefault();
        if(!this.state.selectedFile)return;
        const data = new FormData();
        data.append('filecsv', this.state.selectedFile, this.state.selectedFile.name);
        axios.post("/api/takecsv", data)
            .then((res) => {
                if(res.status === 200){
                    this.setState({
                        success: true
                    })
                }
            })
            .catch(e=>{
                this.setState({
                    error: true
                })

            });
    };

    render(){
        const {error, success} = this.state;
        return(
            <div className='sendCSV'>
                <input
                    id="filecsv"
                    type="file"
                    name="filecsv"
                    onChange={this.handleFileChange}
                />
                <button onClick={this.sendCSV}>Send</button>
                {error && (<i style={{color: 'red'}}>Error.</i>)}
                {success && (<i style={{color: 'green'}}>Success, file saved.</i>)}
            </div>
        )
    }
}

export default saveFile;