import React, {Component} from 'react';

import SaveFile from './saveFile';
import DownloadCSV from './downloadCSV';
import GetJSON from './getJSON';

class App extends Component{
    render(){
        return(
            <div className='mainData'>
                <SaveFile/>
                <DownloadCSV/>
                <GetJSON/>
            </div>
        )
    }
}

export default App;