import React, {Component} from 'react';
import axios from 'axios';

class GetJSON extends Component{
    constructor(){
        super();
        this.state = {
            dataJSON: null,
        }
    }

    getJSON = () =>{
        axios.post('/api/takejson')
            .then(res=>{
                this.setState({
                    dataJSON: res
                })
            })
    };

    cleanTable = () =>{
      this.setState({
          dataJSON: null
      })
    };

    render(){
        const {dataJSON} = this.state;
        return(
            <div className='getJSON'>
                <div>
                    <button onClick={this.getJSON}>View JSON</button>
                    <div>
                        {dataJSON && (
                            <div>
                                <button onClick={this.cleanTable}>Clean</button>
                                <table>
                                    <thead>
                                    <tr>
                                        <td>UserName</td>
                                        <td>FirstName</td>
                                        <td>LastName</td>
                                        <td>Age</td>
                                    </tr>
                                    </thead>
                                    <tbody>
                                    {
                                        dataJSON.data.map((item, i)=>(
                                            <tr key={i}>
                                                <td>{item.UserName}</td>
                                                <td>{item.FirstName}</td>
                                                <td>{item.LastName}</td>
                                                <td>{item.Age}</td>
                                            </tr>
                                        ))
                                    }
                                    </tbody>
                                </table>
                            </div>
                        )}
                    </div>
                </div>
            </div>
        )
    }
}

export default GetJSON;